:root {
  --color-white: #fff;
  --color-black: #000;
  --color-brown: #c79002;
  --color-accent-yellow: #f3c653;
  --color-logo: #494848;
  --color-paragraph-gray: #5f5f5f;
  --color-background: #605b5b;
  --color-background-lemon: #fffdee;
  --color-background-dark-lemon: #f0ecd6;
  --color-background-footer: #0e0c07;
  --color-gray: #b4b4b4;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: Times New Roman, Raleway, Film P3, sans-serif;
}

ul {
